<template>

<div class="container">
  <img class="qs-logo" src="../assets/whoami.png" alt="Who Am I logo">
  <h2 v-if="showFact">{{questions[questionKey].fact}}</h2>

  <hr />
  <div class="flex-container">
    <button :class="{
        'btn': true, 
        'btn-active': (choice === 0), 
        'btn-wrong': (choice === 0 && correctOption !== 0 && correctOption !== -1), 
        'btn-right': (correctOption === 0)
        }" @click="setChoice(0)">  
      <img class="btn-img" :src="firstPM.pic" alt="First PM picture">
      {{firstPM.name}}
    </button>
    <button :class="{
        'btn': true, 
        'btn-active': (choice === 1), 
        'btn-wrong': (choice === 1 && correctOption !== 1 && correctOption !== -1), 
        'btn-right': (correctOption === 1)
        }" @click="setChoice(1)">
      <img class="btn-img" :src="secondPM.pic" alt="Second PM picture">
      {{secondPM.name}}
    </button>
    <button :class="{
        'btn': true, 
        'btn-active': (choice === 2), 
        'btn-wrong': (choice === 2 && correctOption !== 2 && correctOption !== -1), 
        'btn-right': (correctOption === 2)
        }" @click="setChoice(2)">
      <img class="btn-img" :src="thirdPM.pic" alt="Third PM picture">
      {{thirdPM.name}}
    </button>
    <button :class="{
        'btn': true, 
        'btn-active': (choice === 3), 
        'btn-wrong': (choice === 3 && correctOption !== 3 && correctOption !== -1), 
        'btn-right': (correctOption === 3)
        }" @click="setChoice(3)">
      <img class="btn-img" :src="fourthPM.pic" alt="Fourth PM picture">
      {{fourthPM.name}}
    </button>
  </div>

  <div class="flex-action-btns-container">
    <button class="action-btn" @click="gotoPreviousQuestion()"> Back </button>
    <button class="action-btn" @click="showTheFact()"> View Fact </button>
    <button class="action-btn" @click="lockSelection()"> Lock </button>
    <button class="action-btn" @click="gotoNextQuestion()"> Next </button>
  </div>

</div>


</template>

<script>

import questionsJson from '../assets/questions.json'
import pmsJson from '../assets/pms.json'

export default {
  name: 'Question',
  data() {
    return {
      questions: questionsJson,   
      pms: pmsJson,
      choice: -1,
      correctOption: -1,
      questionKey: 0,
      showFact: false      

    }
  },
  
  methods: {
    setChoice(selectedOption) {
      this.choice = selectedOption;
      this.correctOption = -1;
    },
    getPM(pmid) {
        for(var i = 0; i < this.pms.length; i++) {
        
          if (this.pms[i].id === pmid) {
            return this.pms[i]
          }
        }
        return {}
    },
    lockSelection() {
     var options = this.questions[this.questionKey].options;

      for(var i = 0; i < options.length; i++) {
        if(options[i].isRight) {
          this.correctOption = i;
        }
      }
      
    },
    gotoNextQuestion() {
      if(this.questionKey < this.questions.length - 1) {
        this.questionKey++;
        this.resetScreen();
      } else {
        console.log('reached end of game');
      }
    },
    gotoPreviousQuestion() {
      this.questionKey--;
      this.resetScreen();
    },
    resetScreen() {
      this.choice = -1;
      this.correctOption = -1;
      this.showFact = false;
    },
    showTheFact() {
      this.showFact = true;
    }
  },
  computed: {
    firstPM() {
        let firstPmId = this.questions[this.questionKey].options[0].pmid;
        return this.getPM(firstPmId);        
    },
    secondPM() {
        let secondPmId = this.questions[this.questionKey].options[1].pmid;
        return this.getPM(secondPmId);        
    },
    thirdPM() {
        let thirdPmId = this.questions[this.questionKey].options[2].pmid;
        return this.getPM(thirdPmId);        
    },
    fourthPM() {
        let fourthPmId = this.questions[this.questionKey].options[3].pmid;
        return this.getPM(fourthPmId);        
    }

  }
}
</script>


<style scoped>


h2 {
    text-align: center;
    padding-bottom: 20px;
}
.qs-logo {
  height: 50px;
  width: 100px;
  display: block;
  margin: 20px auto;

}
.flex-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.flex-action-btns-container {
  display: flex;
  width: 600px;
  margin: 20px auto;
  padding-top: 20px;
  justify-content: space-around;
  align-items: center;
}
.action-btn {
	box-shadow: 0px 10px 14px -7px #276873;
	background:linear-gradient(to bottom, #599bb3 5%, #408c99 100%);
	background-color:#599bb3;
	border-radius:8px;
	display:inline-block;
	cursor:pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:20px;
	font-weight:bold;
	padding:13px 32px;
	text-decoration:none;
	text-shadow:0px 1px 0px #3d768a;
}
.action-btn:hover {
	background:linear-gradient(to bottom, #408c99 5%, #599bb3 100%);
	background-color:#408c99;
}
.action-btn:active {
	position:relative;
	top:1px;
}

.btn {
  display: block;
  background: rgb(234, 238, 243);
  color: rgb(226, 98, 12);
  border: 2 px solid rgb(226, 98, 12);
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  font-size: 15px;
  font-family: inherit;
  font-weight: bold;
  width: 200px;
}
.btn:hover {
  background: royalblue;
  color: white;
}
.btn:focus {
  outline: none;
}
.btn-active {
  background: royalblue;
  color: white;
  transform: scale(0.98);
}
.btn-block {
  display: block;
  width: 100%;
}
.btn-wrong {
  background-color: red;
}
.btn-right {
  background-color: darkgreen;
}
.btn-img {
  display: block;
  width: 70px;
  height: 70px;
  border-radius: 10px;
  margin: 10px auto;
}
hr {
  margin: 20px auto;
}
</style>
